.step-form-block {
    @include margin-top(-330);

    .title {
        @include fontsize(35);

        text-align: center;
        font-weight: 300;
        color: $white;
    }

    .step-form-container {
        background-color: $white;

        @include margin-top(50);

        .step-form-heading {
            background-color: $theme;
            text-align: center;

            @include padding(50 0);

            color: $white;

            h3.title {
                @include fontsize(28);

                font-weight: 400;
            }

            &.h-sm {
                @include padding(30 0 25);
            }
        }

        .step-form-data {
            @include padding(25 0);
        }

        .step-form-field-box {
            max-width: 500px;
            margin: 0 auto;
        }

        .step-form-nav {
            max-width: 430px;
            margin: 0 auto;

            ul {
                display: table;
                width: 100%;

                li {
                    float: left;
                    width: 50%;

                    @include pos(rel, null);

                    z-index: 0;

                    .step-nav-box {
                        width: 100px;
                        height: 100px;

                        @include circle;

                        background-color: $white;
                        text-align: center;

                        @include border(2, dashed, #d7d7d7);

                        display: table;
                        -webkit-transition: all 600ms cubic-bezier(0.6, -0.28, 0.735, 0.045);
                        transition: all 600ms cubic-bezier(0.6, -0.28, 0.735, 0.045);

                        .step-nav-txt {
                            display: table-cell;
                            vertical-align: middle;
                        }

                        .label-step {
                            display: $b;

                            @include fontsize(22);

                            font-weight: 500;
                            line-height: 1.2;
                            color: lighten($color-text-01, 30%);
                        }

                        .step-no {
                            display: $b;

                            @include fontsize(28);

                            font-weight: 500;
                            line-height: 1.2;
                            color: lighten($color-text-01, 30%);
                        }
                    }

                    .step-one {
                        &:after {
                            @include pos(ab, 50% 0 0 0);

                            content: "";
                            height: 2px;

                            @include border-top(2, dashed, #d7d7d7);

                            width: 100%;
                            z-index: -1;
                        }
                    }

                    .step-two {
                        float: right;

                        &:after {
                            @include pos(ab, 50% 0 0 null);

                            content: "";
                            height: 2px;

                            @include border-top(2, dashed, #d7d7d7);

                            width: 100%;
                            z-index: -1;
                        }
                    }

                    .active {
                        @include border(2, solid, $theme);

                        background-color: $theme;

                        .label-step {
                            color: $white;
                        }

                        .step-no {
                            color: $white;
                        }

                        &:after {
                            border-color: #aeaeae;
                        }
                    }
                }
            }
        }

        .field-group {
            @include margin-top(80);

            .cover_img_field {
                .fileUpload {
                    width: 150px;
                    display: $ib;
                    vertical-align: middle;
                    @include margin-right(10);
                    @include margin-bottom(10);
                    .icon-box {
                        width: 150px;
                        height: 100px;
                        background-color: #eeeeee;
                        @include border(1, solid, #b8b8b8);
                        @include border-round(4);
                        line-height: 100px;
                        text-align: center;
                        @include fontsize(20);
                    }
                }
                .data-list {
                    display: $ib;
                    vertical-align: middle;
                    @include margin-bottom(10);
                    .img-box {
                        @include pos(rel, null);
                        figure {
                            @include margin(0);

                            img {
                                object-fit: cover;
                            }
                        }
                        .delete-ico {
                            width: 15px;
                            height: 15px;
                            text-align: center;
                            line-height: 15px;
                            background-color: $theme;
                            @include circle;
                            @include pos(ab, -8px -5px null null);
                            cursor: pointer;
                            color: $white;
                            @include fontsize(7);
                        }
                    }
                }
            }
        }
    }
}

