.profile-section {
   @include padding(70 0 50);

   .p-top-block {
      text-align: right;

      .icon {
         width: 50px;
         height: 50px;

         @include pos(rel, null);

         @include circle;

         background-color: #e1e1e1;
         line-height: 50px;
         text-align: center;
         color: #797979;
         display: $ib;

         @include fontsize(20);

         @include margin-right(5);

         @include margin-left(20);

         .p-opt__dropbox {
            min-width: 180px;
            background-color: $white;

            @include box-shadow(0 0 16px rgba(0, 0, 0, 0.11));

            @include pos(ab, 55px 0 null null);

            z-index: 10;

            & > ul {
               & > li {
                  & > span {
                     display: $b;
                     cursor: pointer;

                     @include padding(20 0);

                     font-weight: 300;

                     @include border-bottom(1, solid, lighten(#bababa, 20%));

                     &:hover,
                     &:focus {
                        color: $theme;
                     }
                  }

                  &:last-child {
                     & > span {
                        border-bottom: 0;
                     }
                  }
               }
            }
         }
      }

      .button {
         @include margin-left(12);
      }
   }

   .profile-block {
      @include margin-top(30);

      .nav-tabs {
         @include padding-left(350);

         & > li {
            @include border-bottom(1, solid, #ebebeb);

            & > a {
               @include fontsize(20);

               @include padding-bottom(25);

               @include margin-bottom(0);

               border: 0;
               color: lighten($color-text-01, 10%);
               font-weight: 500;
               display: $b;
               vertical-align: middle;
               -webkit-transform: perspective(1px) translateZ(0);
               transform: perspective(1px) translateZ(0);

               //box-shadow: 0 0 1px transparent;
               position: relative;
               overflow: hidden;

               &:before {
                  content: "";
                  position: absolute;
                  z-index: -1;
                  left: 0;
                  right: 100%;
                  bottom: 0;
                  background: $theme;
                  height: 4px;
                  -webkit-transition-property: right;
                  transition-property: right;
                  -webkit-transition-duration: 0.3s;
                  transition-duration: 0.3s;
                  -webkit-transition-timing-function: ease-out;
                  transition-timing-function: ease-out;
               }

               &:after {
                  @include pos(ab, null null -10px 50%);

                  -webkit-transition: all 300ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
                  transition: all 300ms cubic-bezier(0.68, -0.55, 0.265, 1.55);

                  @include translatepos(-50%, 0);

                  content: "";

                  @include arrow(top, $theme, 10px);
               }

               &:hover:before,
               &:focus:before,
               &:active:before {
                  right: 0;
               }

               &:hover:after,
               &:focus:after,
               &:active:after {
                  bottom: 0;
               }

               &:hover,
               &:focus {
                  color: $black;
                  background: none;
                  border: 0;
               }
            }

            &.active {
               & > a {
                  color: $black;
                  background: none;

                  &:before {
                     right: 0;
                  }

                  &:after {
                     bottom: 0;
                  }
               }
            }
         }
      }

      .usr-pic {
         text-align: center;
         display: block;
         margin: 0 auto;
         overflow: hidden;

         @include square(200px);

         @include circle;

         @include border(3, solid, $theme);

         @include margin-bottom(10);

         img {
            object-fit: cover;
            display: block;
            max-width: 100%;
            min-height: 200px;
            min-width: 200px;
            max-height: 200px;
         }

         .u-name {
            h3 {
               @include fontsize(18);

               font-weight: 400;
               color: $color-text-02;
            }
         }

         .u-profile {
            font-weight: 500;

            @include fontsize(16);

            color: $theme;
         }

         .u-location {
            @include fontsize(16);

            font-weight: 400;
            color: $color-text-02;

            @include margin-top(10);
         }
      }

      .profile-block__container {
         @include margin-top(30);

         .profile-block__content__row {
            @include border-bottom(2, solid, #efefef);

            @include padding(25 0);

            &:first-of-type {
               @include padding-top(0);
            }
         }

         .usr-name {
            h3 {
               @include fontsize(30);

               font-weight: 400;
               color: $color-text-02;
            }
         }

         .usr-profile {
            font-weight: 500;

            @include fontsize(16);

            color: $theme;
         }

         .usr-dtl {
            ul {
               li {
                  @include padding-bottom(10);

                  color: $color-text-02;

                  .label-name {
                     color: $color-text-01;

                     @include padding-right(5);
                  }
               }
            }
         }

         .usr-oh-info {
            color: $color-text-02;

            h4 {
               @include fontsize(20);

               font-weight: 400;
               color: $theme;

               @include padding-bottom(10);
            }
         }

         .usr-work-info {
            ul {
               li {
                  display: $ib;
                  vertical-align: middle;
                  color: $color-text-02;

                  @include fontsize(16);

                  font-weight: 500;

                  &.w-label {
                     @include padding-right(90);

                     @include margin-top(10);
                  }

                  &.w-time {
                     @include margin-top(10);
                  }

                  &.v-link {
                     float: right;
                  }
               }
            }
         }

         .usr-tp-container {
            @include margin-top(30);

            .usr-tp-block {
               @include padding(25 0 0);

               @include border-top(2, solid, #efefef);

               h2.title {
                  @include fontsize(28);

                  color: $theme;
                  font-weight: 400;
               }

               .usr-tp__list {
                  @include margin-top(35);
               }

               .usr-tp__box {
                  min-width: 180px;
                  display: $ib;

                  h3 {
                     @include fontsize(16);

                     font-weight: 300;
                     color: lighten($color-text-01, 5%);
                  }

                  .t-no {
                     width: 100px;
                     height: 100px;

                     @include margin(15 0);

                     @include circle;

                     color: $white;
                     display: $ib;
                     line-height: 100px;

                     @include padding(0 10);

                     text-overflow: ellipsis;
                     white-space: nowrap;
                     overflow: hidden;

                     @include fontsize(30);

                     font-weight: 500;
                     text-align: center;

                     &.c-1 {
                        background-color: $color-list-01;
                     }

                     &.c-2 {
                        background-color: $color-list-02;
                     }

                     &.c-3 {
                        background-color: $color-list-03;
                     }

                     &.c-4 {
                        background-color: $color-connetion-03;
                     }

                     &.c-5 {
                        background-color: $color-connetion-05;
                     }

                     &.c-6 {
                        background-color: $color-connetion-08;
                     }
                  }
               }

               &:first-of-type {
                  border: 0;
                  padding-top: 0;
               }
            }
         }

         .about-block {
            h2.title {
               @include fontsize(28);

               color: $theme;
               font-weight: 400;

               @include padding-bottom(10);
            }

            p {
               color: $color-text-02;
               font-weight: 300;
            }
         }

         .profile-block__content {
            @include margin-top(20);
         }
      }

      .profile-post__container {
         @include margin-top(70);
      }

      .profile-people__container {
         @include margin-top(70);
      }
   }
}